import request from '@/utils/axios';
import to from 'await-to-js';
import { useTargetState } from './../stores/index';

export {
    IListUserRepoItemReq,
    IListUserRepoItemRes,
    IGetRepoContentItemRes,
    IPublicUserInfoRes,
    getUserInfo,
    listUserRepo,
    getPublicUserInfo,
    getRepoContent,
    getRepoOneLevel,
    searchCode,
};

const defaultUsername = useTargetState().value.username;

async function getUserInfo() {
    return to(request.get('/user'));
}
interface IListUserRepoItemReq {
    page?: number; // 页数
    per_page?: number; //每页显示个数
    direction?: 'asc' | 'desc'; // 降序 或  升序
    sort?: 'created' | 'updated' | ' pushed' | 'full_name'; // 排序方式：创建时间、更新时间、推送时间，仓库名称
    username?: string;
}
interface IListUserRepoItemRes {
    id: number;
    node_id: string;
    name: string; // 仓库名
    full_name: string; // 仓库全名 ：用户名/仓库名
    // owner: {
    //     login: string; // 仓库所属用户的用户名
    //     url: string; // 获取用户信息 api 地址 -- https://api.github.com/users/ashjian
    //     repos_url: string; // 获取仓库信息 api 地址 -- https://api.github.com/users/ashjian/repos
    // };
    size: number;
    html_url: string; // 仓库web地址
    updated_at: string;
    created_at: string;
    children?: Array<any>;
}

interface IPublicUserInfoRes {
    email: string;
    public_repos: number;
    created_at: string;
    updated_at: string;
}
interface IGetRepoContentItemRes {
    name: string;
    path: string;
}

async function getPublicUserInfo(username = defaultUsername) {
    return to(request.get<IPublicUserInfoRes>(`/users/${username}`));
}

async function listUserRepo({
    page = 1,
    per_page = 30,
    direction = 'desc',
    sort = 'created',
    username = defaultUsername,
}: IListUserRepoItemReq) {
    return to(
        request.get<Array<IListUserRepoItemRes>>(`/users/${username}/repos`, {
            params: {
                page,
                per_page,
                direction,
                sort,
            },
        }),
    );
}

async function getRepoContent(
    repo: string,
    path = '',
    username = defaultUsername,
) {
    return to(
        request.get<Array<IGetRepoContentItemRes>>(
            `/repos/${username}/${repo}/contents/${path}`,
        ),
    );
}

async function getRepoOneLevel(url: string) {
    return to(request.get(url));
}

interface ISearchCodeRes {}

async function searchCode(
    page = 1,
    per_page = 30,
    q = '.ts+user:ashjian',
    sort = 'indexed',
    order = 'desc',
) {
    return to(
        request.get('/search/code', {
            params: {
                q,
                page,
                per_page,
                sort,
                order,
            },
        }),
    );
}
