import { getUserInfo } from '@/apis';
import userLoading from '@/hooks/useLoading';
import router from '@/routers';
import { useAuthState } from '@/stores';

const WhiteList = ['/login'];

router.beforeEach(async (to: any) => {
    const auth = useAuthState();

    if (WhiteList.includes(to.path)) {
        return true;
    }

    if (!auth.value) {
        return {
            name: 'login',
            query: {
                redirect: to.fullPath, // redirect是指登录之后可以跳回到redirect指定的页面
            },
            replace: true,
        };
    } else {
        const loading = userLoading();

        const [error] = await getUserInfo();
        if (error) {
            loading.close();
            return {
                name: 'login',
                query: {
                    redirect: to.fullPath, // redirect是指登录之后可以跳回到redirect指定的页面
                },
                replace: true,
            };
        }
        loading.close();
        return true;
    }
});
